import {
  ChangeDetectionStrategy,
  Component,
  EventEmitter,
  Input,
  Output,
  QueryList,
  ViewChildren
} from '@angular/core';
import { ControlValueAccessor, NG_VALUE_ACCESSOR } from '@angular/forms';
import { InputCheckboxComponent } from '@sofico-framework/ui-kit/components/input-checkbox';
import {
  OnSofFocus,
  SOF_FOCUS_COMPONENT
} from '@sofico-framework/ui-kit/directives/focus';
import { BehaviorSubject, combineLatest, Observable } from 'rxjs';
import { filter, map } from 'rxjs/operators';

interface EnhancedOption {
  id: string;
  label: string;
  selected?: boolean;
}

@Component({
  selector: 'sof-input-checkbox-list',
  changeDetection: ChangeDetectionStrategy.OnPush,
  styleUrls: ['./input-checkbox-list.component.scss'],
  template: `
    <sof-input-checkbox
      #checkboxes
      *ngFor="let option of enhancedOptions$ | async; trackBy: trackByFn"
      [label]="option.label"
      [selected]="option.selected"
      [isDisabled]="isDisabled"
      (changeValue)="onChange(option, option.selected)"
    ></sof-input-checkbox>
  `,
  providers: [
    {
      provide: NG_VALUE_ACCESSOR,
      useExisting: InputCheckboxListComponent,
      multi: true
    },
    { provide: SOF_FOCUS_COMPONENT, useExisting: InputCheckboxListComponent }
  ]
})
export class InputCheckboxListComponent
  implements ControlValueAccessor, OnSofFocus {
  /**
   *  Determines if the input is disabled.
   */
  @Input() isDisabled: boolean;

  private options$ = new BehaviorSubject([]);

  /**
   *  Sets the the available options (checkboxes).
   */
  @Input() set options(v: { label: string; id: string }[]) {
    this.options$.next(v);
  }

  private values$ = new BehaviorSubject<string[]>([]);

  /**
   * Determines whether the input is in a valid state.
   */
  @Input() invalid: boolean;

  /**
   * Determines if the input is checked or not.
   */
  @Input() set value(value: string[]) {
    this.writeValue(value);
  }

  /**
   * EventEmitter that will emit the value when changed.
   */
  @Output() changeValue = new EventEmitter<string[]>();

  @ViewChildren('checkboxes') checkboxes: QueryList<InputCheckboxComponent>;

  enhancedOptions$: Observable<EnhancedOption[]> = combineLatest([
    this.options$.pipe(filter(v => !!v)),
    this.values$
  ]).pipe(
    map(([options, values]) => {
      return options.map(option => {
        return {
          id: option.id,
          label: option.label,
          selected: values?.indexOf(option.id) > -1
        };
      });
    })
  );

  propagateChange: any;
  propagateTouch: any;

  trackByFn = i => i;

  sofFocus(): void {
    this.checkboxes.first?.sofFocus();
  }

  registerOnChange(fn: any): void {
    this.propagateChange = fn;
  }

  registerOnTouched(fn: any): void {
    this.propagateTouch = fn;
  }

  setDisabledState(isDisabled: boolean): void {
    this.isDisabled = isDisabled;
  }

  writeValue(list: string[]): void {
    this.values$.next(list);
  }

  onChange(option: { label: string; id: string }, selected: boolean): void {
    if (!this.isDisabled) {
      const oldValues = this.values$.getValue() ?? [];
      const newValues = selected
        ? oldValues.filter(value => value !== option.id)
        : [...oldValues, option.id];
      this.values$.next(newValues);
      if (this.propagateChange) {
        this.propagateChange(newValues);
      }
    }
  }
}
